<template>
  <div class="calendar">
    <div class="top">
      <div class="month">
        <button @click="prevMonth">
          <svg viewBox="0 0 32 32" height="1em" fill="currentColor">
            <path d="M20 25a1 1 0 0 1-.71-.29l-8-8a1 1 0 0 1 0-1.42l8-8a1 1 0 1 1 1.42 1.42L13.41 16l7.3 7.29A1 1 0 0 1 20 25z" />
          </svg>
        </button>
        <div>{{ monthName }}</div>
        <button @click="nextMonth">
          <svg viewBox="0 0 32 32" height="1em" fill="currentColor">
            <path d="M12 7a1 1 0 0 1 .71.29l8 8a1 1 0 0 1 0 1.42l-8 8c-.392.392-1.028.392-1.42 0s-.392-1.028 0-1.42l7.3-7.29-7.3-7.29A1 1 0 0 1 12 7z" />
          </svg>
        </button>
      </div>
      <div class="dow">
        <div v-for="day in days" :key="day" class="day">{{ day }}</div>
      </div>
    </div>
    <div class="days">
      <div v-for="(week, id) in calendarDays" :key="`week-${id+1}`" class="week">
        <div
          v-for="day in week"
          :key="day.day"
          :class="{extra: !day.currentMonth, active: day.active}"
          class="day"
          @click="update(day.date)"
        >
          {{ day.date.getDate() }}
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, ref, watch } from '@vue/runtime-core';
import { dateToISO, formatDate, toDate } from '@/filters';

export default defineComponent({
  props: {
    modelValue: String,
  },
  emits: ['update:modelValue'],
  setup(props) {
    const current = computed(() => toDate(props?.modelValue));
    const day = ref(current.value.getDate());
    const month = ref(current.value.getMonth());
    const year = ref(current.value.getFullYear());
    watch(current, () => {
      day.value = current.value.getDate();
      month.value = current.value.getMonth();
      year.value = current.value.getFullYear();
    });

    //calculating limits of calendar days
    const firstDayOfMonth = computed(() => new Date(year.value, month.value, 1));
    const monthName = computed(() => formatDate(firstDayOfMonth.value, {month: 'long', year: 'numeric'}));
    const lastDayOfMonth = computed(() => new Date(year.value, month.value + 1, 0));
    // const lastDayOfPrevMonth = computed(() => new Date(year.value, month.value, 0));
    const startDay = computed(() => 1 - firstDayOfMonth.value.getDay());
    const endDay = computed(() => {
      const dow = lastDayOfMonth.value.getDay();
      return lastDayOfMonth.value.getDate() + (6 - dow) % 7;
    });
    const calendarDays = computed(() => {
      const chunked: CalendarDay[][] = [];
      let days: CalendarDay[] = [];
      for (let day = startDay.value; day <= endDay.value; day++) {
        const date = new Date(year.value, month.value, day);
        days.push({
          day,
          date,
          currentMonth: day > 0 && day <= lastDayOfMonth.value.getDate(),
          active: dateToISO(date) === dateToISO(current.value),
        });
        if (days.length === 7) {
          chunked.push(days);
          days = [];
        }
      }
      return chunked;
    });

    //constants
    const days = ['Su', 'Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa'];

    return { calendarDays, current, days, day, month, year, monthName };
  },
  methods: {
    prevMonth() {
      if (!this.month) this.year--;
      this.month = (this.month + 11) % 12;
    },
    nextMonth() {
      this.month = (this.month + 1) % 12;
      if (!this.month) this.year++;
    },
    update(date: Date) {
      this.$emit('update:modelValue', dateToISO(date));
    },
  },
});

interface CalendarDay {
  day: number;
  date: Date;
  currentMonth: boolean;
  active: boolean;
}
</script>

<style lang="scss" scoped>
.calendar {
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
}
.month {
  display: flex;
  padding: .5em;
  justify-content: space-between;
  button {
    background-color: transparent;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 16px;
    line-height: 0;
    padding: .25em;

    &:hover {
      background-color: #9993;
    }
  }
}
.top {
  background-color: #eee;
  border: 1px solid #ccc;
}
.dow {
  display: flex;
}
.week {
  display: flex;
}
.day {
  box-sizing: content-box;
  flex-grow: 1;
  padding: .1em;
  text-align: center;
  width: 1.75em;
  &.extra {
    color: #ccc;
  }
  &.active {
    background-color: #015e99;
    color: #fff;
  }
  .week & {
    cursor: pointer;
    border: 1px solid #ccc;
    border-top: 0;

    & + .day {
      border-left: 0;
    }
  }
}
</style>
