<template>
  <div class="checkbox-item">
    <label>
      <div class="input-wrap">
        <input v-model="model" :name="name" :value="value" type="checkbox" />
        <span class="input-state">
          <svg xmlns="http://www.w3.org/2000/svg" width="11.703" height="8.727" viewBox="0 0 11.703 8.727">
            <path d="M3.975,73.654l-3.8-3.8a.585.585,0,0,1,0-.828L1,68.2a.585.585,0,0,1,.828,0l2.562,2.562L9.877,65.27a.585.585,0,0,1,.828,0l.827.828a.585.585,0,0,1,0,.828L4.8,73.654a.585.585,0,0,1-.828,0Z" transform="translate(0 -65.098)" fill="#0c0c0c" />
          </svg>
        </span>
      </div>
      <span class="text">
        <slot>{{ value }}</slot>
      </span>
    </label>
  </div>
</template>

<script>
export default {
  name: 'CheckboxItem',
  props: {
    name: String,
    useValue: Boolean,
    value: String,
  },
  emits: ['update:modelValue'],
  computed: {
    model: {
      get() {
        return this.useValue ? this.$attrs?.modelValue === this.value : this.$attrs?.modelValue;
      },
      set(value) {
        this.$emit('update:modelValue', this.useValue ? (value ? this.value : this.empty) : value);
      },
    },
    empty() {
      return this.$attrs?.emptyValue !== undefined ? this.$attrs?.emptyValue : null;
    },
  },
};
</script>

<style scoped>
.checkbox-item label {
  display: flex !important;
}
</style>
