import { createRouter, createWebHashHistory, RouteRecordRaw } from 'vue-router';
import Assignments from '../views/Assignments.vue';

const routes: RouteRecordRaw[] = [
  {
    path: '/',
    name: 'Home',
    redirect: { name: 'Assignments' },
  },
  {
    path: '/assignments',
    name: 'Assignments',
    component: Assignments,
  },
  {
    path: '/assignments/create',
    name: 'AssignmentsCreate',
    component: () => import(/* webpackChunkName: "assignments-create" */ '@/views/Assignments/Create.vue'),
  },
  {
    path: '/assignments/:id/edit',
    name: 'AssignmentsEdit',
    component: () => import(/* webpackChunkName: "assignments-edit" */ '@/views/Assignments/Edit.vue'),
    props: true, //insert the param as a prop
  },
  {
    path: '/assignments/:id',
    name: 'AssignmentsView',
    component: () => import(/* webpackChunkName: "assignments-edit" */ '@/views/Assignments/View.vue'),
    props: true, //insert the param as a prop
  },
  {
    path: '/library',
    name: 'Library',
    component: () => import(/* webpackChunkName: "library" */ '@/views/Library.vue'),
  },
  {
    path: '/library/create',
    name: 'LibraryCreate',
    component: () => import(/* webpackChunkName: "library-create" */ '@/views/Library/Create.vue'),
  },
  {
    path: '/library/:id/edit',
    name: 'LibraryEdit',
    component: () => import(/* webpackChunkName: "library-edit" */ '@/views/Library/Edit.vue'),
    props: true, //insert the param as a prop
  },
  {
    path: '/library/:id',
    name: 'LibraryView',
    component: () => import(/* webpackChunkName: "library-edit" */ '@/views/Library/View.vue'),
    props: true, //insert the param as a prop
  },
  {
    path: '/library/by-me',
    name: 'LibraryByMe',
    component: () => import(/* webpackChunkName: "library-by-me" */ '@/views/Library/ByMe.vue'),
  },
  {
    path: '/library/by-math-nation',
    name: 'LibraryByMathNation',
    component: () => import(/* webpackChunkName: "library-by-math-nation" */ '@/views/Library/ByMathNation.vue'),
  },
  {
    path: '/library/by-district',
    name: 'LibraryByDistrict',
    component: () => import(/* webpackChunkName: "library-by-district" */ '@/views/Library/ByDistrict.vue'),
  },
  {
    path: '/library/by-others',
    name: 'LibraryByOthers',
    component: () => import(/* webpackChunkName: "library-by-others" */ '@/views/Library/ByOthers.vue'),
  },
  {
    path: '/example',
    name: 'Example',
    component: () => import(/* webpackChunkName: "library" */ '@/examples/views/Home.vue'),
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

export default router;
