/** default currency */ const DC = 'USD';
/** default digits   */ const DD = '2-digit';
/** default digits   */ const DL = 'en-US';

type Dateable = string | number | Date | null;
type Locales = string | string[];

export function currency(value: any, locales: Locales = DL, currency: string = DC) {
  return parseFloat(value)?.toLocaleString?.(locales, {style: 'currency', currency});
}

export function date(value: Dateable = null, locales: Locales = DL) {
  return toDate(value).toLocaleDateString(locales, {day: DD, month: DD, year: 'numeric', timeZone: 'UTC'});
}

export function formattedDate(value: Dateable = null, locales: Locales = DL) {
  return date(value, locales);
}

export function datetime(value: Dateable = null, locales: Locales = DL) {
  return toDate(value).toLocaleString(locales, {day: DD, month: DD, year: 'numeric', hour: DD, minute: DD, hour12: true});
}

export function formatDate(value: Dateable = null, format: Intl.DateTimeFormatOptions, locales: Locales = DL) {
  return new Intl.DateTimeFormat(locales, format).format(toDate(value));
}

export function longDate(value: Dateable = null, locales: Locales = DL) {
  return formatDate(value, {
    weekday: 'long',
    year: 'numeric',
    month: 'long',
    day: '2-digit',
  }, locales);
}

const dateISO = (value: Dateable = null) => toDate(value).toISOString();
export const dateToISO = (value: Dateable = null, full: boolean = false) => (full ? dateISO(value) : dateISO(value).substr(0, 10));

/** Get a date (null or invalid returns the current date) */
export function toDate(value: Dateable = null) {
  const newDate = !(value instanceof Date) && value !== null;
  const date = value === null ? new Date() : (value instanceof Date ? value : new Date(value));
  if (newDate) date.setMinutes(date.getTimezoneOffset());
  return isNaN(date.getTime()) ? new Date() : date;
}

export function unixToDateTime(value: number) {
  return datetime(new Date(value * 1000));
}
