<template>
  <router-link v-slot="{ href, route, navigate }" :to="to" custom>
    <a
      :href="href"
      :class="[$attrs.class, !active ? {} : {
        [active]: isActive(route.fullPath),
      }]"
      @click="navigate"
    >
      <slot>
        {{ route.fullPath }}
      </slot>
    </a>
  </router-link>
</template>

<script lang="ts">
import { defineComponent, PropType } from 'vue';
import { RouteLocationRaw } from 'vue-router';

export default defineComponent({
  props: {
    active: String,
    exact: Boolean,
    to: {
      type: [String, Object] as PropType<RouteLocationRaw>,
      required: true,
    },
  },
  methods: {
    isActive(path: string): boolean {
      if (this.exact) return this.$route?.fullPath === path;
      return this.$route?.fullPath?.startsWith(path);
    },
  },
});
</script>

