<template>
  <div class="radio-item">
    <label>
      <div class="input-wrap style-2">
        <input v-model="model" :name="name" :value="value" type="radio" />
        <span class="input-state"></span>
      </div>
      <span class="text">
        <slot>{{ value }}</slot>
      </span>
    </label>
  </div>
</template>

<script>
export default {
  name: 'RadioItem',
  props: {
    name: String,
    value: String,
  },
  emits: ['update:modelValue'],
  computed: {
    model: {
      get() {
        return this.$attrs.modelValue;
      },
      set(value) {
        this.$emit('update:modelValue', value);
      },
    },
  },
};
</script>

<style scoped>
.radio-item label {
  display: flex !important;
}
</style>
